@font-face {
   font-family: 'Noto Sans Japanese';
   font-style: normal;
   font-weight: 300;
   src: url(//fonts.gstatic.com/ea/notosansjapanese/v6/NotoSansJP-DemiLight.woff2) format('woff2'),
        url(//fonts.gstatic.com/ea/notosansjapanese/v6/NotoSansJP-DemiLight.woff) format('woff'),
        url(//fonts.gstatic.com/ea/notosansjapanese/v6/NotoSansJP-DemiLight.otf) format('opentype');
}

* {
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
  font-family: 'Noto Sans Japanese', sans-serif;
}

body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  background-color: #555555;
}

#screen-switch * {
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.start-button.enabled:hover {
  background-color: #F57C00 !important;
}

/* load circles */
@-webkit-keyframes loading-animation {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes loading-animation {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

/* arrow */
.up-arrow-animation {
  -webkit-animation: up-arrow-animation infinite 0.4s;
          animation: up-arrow-animation infinite 0.4s;
}
.down-arrow-animation {
  -webkit-animation: down-arrow-animation infinite 0.4s;
          animation: down-arrow-animation infinite 0.4s;
}
.left-arrow-animation {
  -webkit-animation: left-arrow-animation infinite 0.4s;
          animation: left-arrow-animation infinite 0.4s;
}
.right-arrow-animation {
	-webkit-animation: right-arrow-animation infinite 0.4s;
	        animation: right-arrow-animation infinite 0.4s;
}

@-webkit-keyframes up-arrow-animation {
    0% {margin-bottom: 0;}
   50% {margin-bottom: 8px;}
  100% {margin-bottom: 0;}
}

@keyframes up-arrow-animation {
    0% {margin-bottom: 0;}
   50% {margin-bottom: 8px;}
  100% {margin-bottom: 0;}
}
@-webkit-keyframes down-arrow-animation {
    0% {margin-top: 0;}
   50% {margin-top: 8px;}
  100% {margin-top: 0;}
}
@keyframes down-arrow-animation {
    0% {margin-top: 0;}
   50% {margin-top: 8px;}
  100% {margin-top: 0;}
}
@-webkit-keyframes left-arrow-animation {
    0% {margin-right: 0;}
   50% {margin-right: 8px;}
  100% {margin-right: 0;}
}
@keyframes left-arrow-animation {
    0% {margin-right: 0;}
   50% {margin-right: 8px;}
  100% {margin-right: 0;}
}
@-webkit-keyframes right-arrow-animation {
    0% {margin-left: 0;}
   50% {margin-left: 8px;}
  100% {margin-left: 0;}
}
@keyframes right-arrow-animation {
    0% {margin-left: 0;}
   50% {margin-left: 8px;}
  100% {margin-left: 0;}
}

.magnifying-glass {
  opacity: 0.6;
}
.magnifying-glass:hover {
  opacity: 1;
}

.button:hover {
  text-shadow: 0 0 5px rgba(255, 255, 255, 1);
}

/* save effect */
#save-effect.animation {
  -webkit-animation: .5s save-animation ease;
          animation: .5s save-animation ease;
}
@-webkit-keyframes save-animation {
  from {
    background-color: rgba(255, 255, 255, 0.8);
  }
  to {
    background-color: transparent;
  }
}
@keyframes save-animation {
  from {
    background-color: rgba(255, 255, 255, 0.8);
  }
  to {
    background-color: transparent;
  }
}



